export default {
	data() {
		return {
			pageNum:0, //当前页数
			mydata:[], //求购单数组
		};
	},
	onLoad() {
		this.myajax()
	},
	methods: {
		//跳转页面
		opennew: function(id) {
			uni.navigateTo({
				url: '../' + id + '/' + id
			});
		},
		// 拉取数据
		myajax:function(){
			var that=this
			this.pageNum++
			uni.showLoading({
			    title: '加载中',
				mask:true
			});
			uni.request({
			    url: 'https://www.example.com/request', //仅为示例，并非真实接口地址。
			    data: {
			        text: 'uni.request'
			    },
			    success: (res) => {
			        console.log(res.data);
					that.mydata.push(res.data)
			    }
			});
			setTimeout(function() {
				uni.hideLoading()
			}, 1000);
		}
	}
};
